@import "../../UI/variables.scss";

.task-modal {
  background-color: $white;
  padding: 24px 24px 32px;
  border-radius: 6px;
  position: absolute;
  width: min(90%, 480px);
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  max-height: calc(100vh - 150px);
  overflow: auto;
}
